@import url(https://fonts.googleapis.com/css?family=Damion|Turret+Road:400,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/FCC-drum-machine/static/media/synthwave-3941721_1920.c99c487a.jpg) no-repeat center center fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body{
  margin:0;
  padding:0;
  color:white;
  
}

.title{
  font-family: 'Turret Road', cursive;
  color: #EE91AE;
  -webkit-text-stroke-width: 1px; 
  -webkit-text-stroke-color: white; 
  -webkit-transform: skewY(-9deg); 
          transform: skewY(-9deg);
  text-shadow: 0 0 23px #FF0000, 0 0 5px #0000FF; 
  font-size: 2.5rem;
  text-align: left;
  padding-left: 1rem;
                 
}

#display{
  margin-top: 3rem;
  text-align: center;
  font-family: 'Damion', cursive;
  font-size: 2.25rem;
  color: #00E6F1;
  
  text-shadow: 0 0 23px #FF0000, 0 0 5px #0000FF;  
}
/* .center-grid{
  display: flex;
