.grid-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.158);
  box-shadow: 10px 50px 20px rgba(0, 0, 0, 0.158) ;
  margin-top:3rem;
  font-family: 'Turret Road', cursive;
  font-size: 1.5rem;
  font-weight: bolder;
  padding:0;
  cursor: pointer;
  text-shadow: 0 0 10px #CA1CA3, 1px 10px 10px #CA1CA3;
}

